/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.review {
  margin-bottom: 110px;
  padding-top: 60px;
}
.review .review_box {
  width: 45%;
  min-width: 700px;
  border: 1px solid #dddddd;
  margin: auto;
}
.review .review_box .title {
  height: 50px;
  line-height: 50px;
  display: flex;
  justify-content: space-around;
  font-size: 1.6em;
}
.review .review_box .title .profile img {
  margin-top: 20px;
  width: 30px;
  height: 30px;
  border: 1px solid #dddddd;
  border-radius: 50px;
  margin-right: 10px;
  margin-top: 10px;
}
.review .review_box .title .profile p {
  font-size: 0.6em;
  float: right;
}
.review .review_box .title span {
  color: #cf5724;
}
.review .review_box .title .star {
  color: #ff8955;
  font-size: 1em;
}
.review .review_box .slider {
  border: 1px solid #dddddd;
  display: flex;
}
.review .review_box .slider .line {
  display: flex;
  width: 560px;
  height: 320px;
  margin: 20px 20px;
  overflow: hidden;
}
.review .review_box .slider .div {
  transition: 0.5s;
  width: auto;
  height: 310px;
  display: flex;
}
.review .review_box .slider .slide {
  text-align: center;
  padding: 10% 0%;
  background: gray;
  width: 560px;
  max-height: inherit;
}
.review .review_box .slider img {
  width: 100%;
  position: relative;
  top: -55px;
}
.review .review_box .slider button {
  color: #fff;
  background: #cf5724;
  border: 1px solid #cf5724;
}
.review .review_box .slider button:hover {
  background: #ff8955;
}
.review .review_box .text_box {
  padding: 20px;
}
.review .review_box .text_box h3 {
  font-size: 1.4em;
}
.review .review_box .text_box p {
  margin: 10px 0;
  font-size: 1em;
  line-height: 1.4em;
}

.go_back {
  display: block;
  color: #fff;
  margin: auto;
  padding: 20px;
  background: #cf5724;
  border: 1px solid #cf5724;
  font-size: 2em;
  position: fixed;
  z-index: 80;
  bottom: 0;
  width: 100%;
}
.go_back button {
  width: 100%;
  text-align: center;
  height: 100%;
  border: 0;
  background: 0;
  color: #fff;
}

.go_back:hover {
  background: #ff8955;
}